@import "./variables.scss";

.Body {
    min-height: 100%;
    background-image: url("https://transparenttextures.com/patterns/asfalt-dark.png");
}

.NavBar {
    width: 100vw;
    height: 75px;
    position: fixed;
    top:0;
    left:0;
    z-index: 1;
}

.blur {
    width: 100vw;
    height: 45px;
    top: 75px;
    background-image: linear-gradient(to bottom, rgba(238, 238, 238, 1), rgba(238, 238, 238, 0));
    position: fixed;
    z-index: 2;
    opacity: 0.75;
}

.BeerContainer {
    margin: 85px 45px 0 45px;
    display: flex;
    flex-wrap: wrap;
    z-index: 0;
    min-height: 90vh;
}

.Footer {
    margin-top: 20px;
    position: relative;
    left: 0;
    bottom: 0;
}